import JQuery from 'jquery';
const $ = JQuery;
let speed = 1000;
let count=1;
const states = ['.landing', '.about ', '.education', '.certifications', '.experience', '.skills', '.contact'];
let state;
/**
	@class MobileNMService
				 Allows Navigation Menu JQuery methods for mobile devices.
				 Designed for dealing with devices up to a maximum width of 768px.

	@last_passed_testing
	@see DesktopNMService Desktop equivalent of this service.
*/
class ScrollService {

	static scrollStateListener() {
			$(window).scroll(function() {
					let isInViewport = function(el) {
						try {
						var top_of_element = $(el).offset().top + 50;
						var bottom_of_element = $(el).offset().top + $(el).outerHeight();
						var bottom_of_screen = $(window).scrollTop() + $(window).innerHeight();
						var top_of_screen = $(window).scrollTop();
						if ((bottom_of_screen > top_of_element) && (top_of_screen < bottom_of_element)){
							return true;
						} else {
							return false;
						}
					} catch (ex) {
						console.log(ex);
					}
					}
					switch (true) {
						case isInViewport('.landing'):
							state = states[0];
							break;
						case isInViewport('.about'):
							state = states[1];
							break;
						case isInViewport('.education'):
							state = states[2];
							break;
						case isInViewport('.certifications'):
							state = states[3];
							break;
						case isInViewport('.experience'):
							state = states[4];
							break;
						case isInViewport('.skills'):
							state = states[5];
							break;
						case isInViewport('.contact'):
							state = states[6];
							break;
					}
			});
	}



	static scrollDown() {
		$(state).click(function() {
			let next = states.indexOf(state) + 1;
			let $a = $('html, body').animate({
				scrollTop: $(states[next]).offset().top + 20
			}, speed);
			setTimeout(function(){
				$a.stop(true, false);
			}, speed);
		});
	}

} export default ScrollService;