<script>
import MainService from '../MainService';
import ScrollService from '../ScrollService';
import JQuery from 'jquery';
let $ = JQuery;
export default {
	name: 'MainComponent',
	data() {
		return {
			certs: [],
			edu: [],
			exp: [],
			pers: [],
			skills: [],
			error: '',
      down() {
        ScrollService.scrollDown();
      },
      home() {
        let $a = $('html, body').animate({
					scrollTop: $('.navbar').offset().top + 0
				}, 1000);
				setTimeout(function(){
					$a.stop(true, false);
				}, 1000);
      }
		};
	},
	async created() {
		try {
			// Populate Data
			this.certs = await MainService.getCerts();
			this.edu = await MainService.getEdu();
			this.exp = await MainService.getExp();
			this.pers = await MainService.getPers();
			this.skills = await MainService.getSkills();
		} catch (err) {
			this.error = err;
		} // Populate Error
	},

};

MainService.initNavbar();
MainService.initDropdown();
MainService.listenForButtonHover();
ScrollService.scrollStateListener();
</script>




